<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/brand/list" @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse='true'>
        <li>带 * 为必填项</li>
      </cms-prompt>
      <el-form ref="form" :rules="saveRules" :model="formData" label-width="210px" @submit.native.prevent>
        <el-form-item label="品牌名称" prop="name" :error='errorMessage.name'>
          <el-input v-model="formData.name" maxlength="150"></el-input>
        </el-form-item>
        <el-form-item label="英文名称" prop="enName" :error='errorMessage.enName'>
          <el-input v-model="formData.enName" maxlength="150"></el-input>
        </el-form-item>
        <el-form-item label="名称首字母" prop="initials" :error='errorMessage.initials'>
          <el-input disabled v-model="formData.initials" maxlength="50"></el-input>
        </el-form-item>
        <el-form-item label="品牌LOGO" prop="logo" :error='errorMessage.logo' class="brands-logo">
          <cms-picture-zone  v-model="formData.logo" :data='formData.resourcesSpaceData'></cms-picture-zone>
        </el-form-item>
        <el-form-item label="是否显示" prop="show">
          <cms-switch v-model="formData.show"></cms-switch>
          <div class="gray">设置品牌是否推荐至品牌橱窗专题列表页醒目显示</div>
        </el-form-item>
        <el-form-item label="排序" prop="sortNum" :error='errorMessage.sortNum' >
          <el-input v-model.number="formData.sortNum"  maxlength="2"> </el-input>
        </el-form-item>
        <el-form-item label="网址" prop="url" :error='errorMessage.url'>
          <el-input v-model="formData.url" maxlength="50"></el-input>
          <div class="gray">填写带访问协议的访问域名</div>
        </el-form-item>
        <el-form-item label="品牌简介" prop="profile">
          <el-input v-model="formData.profile" type="textarea" :rows="6"  maxlength="400"></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="update" v-color>保存</cms-button>
        <cms-button type='rest' @click="reset">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import pinyin from 'js-pinyin'
import rules from '@/rules'
import form from '@/mixins/form.js'
import { brandUpdate, brandGet } from '@/api/product/brand.js'
var defaultData = {
  id: '',
  name: '',
  enName: '',
  initials: '',
  logo: '',
  show: '',
  sortNum: '',
  url: '',
  profile: ''
}
export default {
  mixins: [form],
  data() {
    return {
      formData: Object.assign({}, defaultData),
      saveRules: {
        // 新增规则
        name: [rules.required('请填写品牌名称'), rules.len(1, 150)],
        initials: [
          rules.required('请填写名称首字母'),
          rules.len(1, 50),
          rules.english()
        ],
        url: [rules.url()],
        logo: [rules.required('请填写品牌LOGO', 'change'), rules.number()],
        sortNum: [rules.required(), rules.number()]
      }
    }
  },
  watch: {
    formData: {
      handler(newV, oldV) {
        pinyin.setOptions({ checkPolyphone: false, charCase: 0 })
        if (newV.name === '') {
          return false
        } else {
          const arr = pinyin.getFullChars(newV.name)
          newV.initials = arr[0].toUpperCase()
        }
      },
      deep: true
    }
  },
  methods: {
    getFormData(id) { // 通过id获取表单信息
      brandGet({ id: id }).then(res => {
        this.formData = Object.assign(defaultData, res.data)
        if (res.data.resourcesSpaceData) {
          this.$set(this.formData, 'logo', res.data.resourcesSpaceData.id)
        }
      })
    },
    update() {
      this.updateForm(brandUpdate, this.formData, '/brand/list', 'list')
    },
    reset() {
      this.getFormData(this.$route.query.id)
      this.$refs.form.resetFields()
    }
  },
  mounted() {
    this.getFormData(this.$route.query.id)
  }
}
</script>



